import parseUserAgent from './user-agent';
var supportedPlatforms = ['mac', 'windows', 'linux'];
var detectedPlatforms = new Set();
// Emphasize content for the visitor's OS (inferred from user agent string)
export default function displayPlatformSpecificContent() {
    var platform = getDefaultPlatform() || parseUserAgent().os;
    // adjust platform names to fit existing mac/windows/linux scheme
    if (!platform)
        platform = 'linux';
    if (platform === 'ios')
        platform = 'mac';
    var platformsInContent = getDetectedPlatforms();
    // when the `defaultPlatform` frontmatter isn't set and the article
    // does not define all platforms in the content, documentation is hidden
    // for users with the undefined platform. This sets a default
    // platform for those users to prevent unintentionally hiding content
    if (!platformsInContent.includes(platform)) {
        // uses the order of the supportedPlatforms array to
        // determine the default platform
        platform = supportedPlatforms.filter(function (elem) { return platformsInContent.includes(elem); })[0];
    }
    showPlatformSpecificContent(platform);
    hideSwitcherLinks(platformsInContent);
    setActiveSwitcherLinks(platform);
    // configure links for switching platform content
    switcherLinks().forEach(function (link) {
        link.addEventListener('click', function (event) {
            event.preventDefault();
            var target = event.target;
            setActiveSwitcherLinks(target.dataset.platform || '');
            showPlatformSpecificContent(target.dataset.platform || '');
        });
    });
}
function setActiveSwitcherLinks(platform) {
    // (de)activate switcher link appearances
    switcherLinks().forEach(function (link) {
        link.dataset.platform === platform
            ? link.classList.add('selected')
            : link.classList.remove('selected');
    });
}
function showPlatformSpecificContent(platform) {
    // find all platform-specific *block* elements and hide or show as appropriate
    // example: {{ #mac }} block content {{/mac}}
    var markdowns = Array.from(document.querySelectorAll('.extended-markdown'));
    markdowns
        .filter(function (el) { return supportedPlatforms.some(function (platform) { return el.classList.contains(platform); }); })
        .forEach(function (el) {
        el.style.display = el.classList.contains(platform) ? '' : 'none';
    });
    // find all platform-specific *inline* elements and hide or show as appropriate
    // example: <span class="platform-mac">inline content</span>
    var platforms = Array.from(document.querySelectorAll('.platform-mac, .platform-windows, .platform-linux'));
    platforms.forEach(function (el) {
        el.style.display = el.classList.contains('platform-' + platform) ? '' : 'none';
    });
}
// hide links for any platform-specific sections that are not present
function hideSwitcherLinks(platformsInContent) {
    var links = Array.from(document.querySelectorAll('a.platform-switcher'));
    links.forEach(function (link) {
        if (platformsInContent.includes(link.dataset.platform || ''))
            return;
        link.style.display = 'none';
    });
}
// gets the list of detected platforms in the current article
function getDetectedPlatforms() {
    // find all platform-specific *block* elements and hide or show as appropriate
    // example: {{ #mac }} block content {{/mac}}
    var allEls = Array.from(document.querySelectorAll('.extended-markdown'));
    allEls
        .filter(function (el) { return supportedPlatforms.some(function (platform) { return el.classList.contains(platform); }); })
        .forEach(function (el) { return detectPlatforms(el); });
    // find all platform-specific *inline* elements and hide or show as appropriate
    // example: <span class="platform-mac">inline content</span>
    var platformEls = Array.from(document.querySelectorAll('.platform-mac, .platform-windows, .platform-linux'));
    platformEls.forEach(function (el) { return detectPlatforms(el); });
    return Array.from(detectedPlatforms);
}
function detectPlatforms(el) {
    el.classList.forEach(function (elClass) {
        var value = elClass.replace(/platform-/, '');
        if (supportedPlatforms.includes(value))
            detectedPlatforms.add(value);
    });
}
function getDefaultPlatform() {
    var el = document.querySelector('[data-default-platform]');
    if (el)
        return el.dataset.defaultPlatform;
}
function switcherLinks() {
    return Array.from(document.querySelectorAll('a.platform-switcher'));
}
