export default function () {
    // Open and close mobile nav
    var hamburgerButton = document.querySelector('.nav-mobile-burgerIcon');
    var mobileDropdown = document.querySelector('.nav-mobile-dropdown');
    if (!(hamburgerButton && mobileDropdown))
        return;
    hamburgerButton.addEventListener('click', function (event) {
        event.preventDefault();
        hamburgerButton.classList.toggle('js-open');
        mobileDropdown.classList.toggle('js-open');
    });
}
