import { EventType, sendEvent } from './events';
export default function () {
    var printButtons = document.querySelectorAll('.js-print');
    Array.from(printButtons).forEach(function (btn) {
        // Open the print dialog when the button is clicked
        btn.addEventListener('click', function () {
            window.print();
        });
    });
    // Track print events
    window.onbeforeprint = function () {
        sendEvent({ type: EventType.print });
    };
}
