import { sendEvent, EventType } from './events';
function showElement(el) {
    el.removeAttribute('hidden');
}
function hideElement(el) {
    el.setAttribute('hidden', 'hidden');
}
function updateDisplay(form, state) {
    var allSelector = ['start', 'yes', 'no', 'end']
        .map(function (xstate) { return '[data-help-' + xstate + ']'; })
        .join(',');
    var stateSelector = '[data-help-' + state + ']';
    var allEls = Array.from(form.querySelectorAll(allSelector));
    allEls.forEach(hideElement);
    var stateEls = Array.from(form.querySelectorAll(stateSelector));
    stateEls.forEach(showElement);
}
function submitForm(form) {
    var formData = new FormData(form);
    return trackEvent(formData);
}
function trackEvent(formData) {
    return sendEvent({
        type: EventType.survey,
        survey_token: formData.get('survey-token') || undefined,
        survey_vote: formData.get('survey-vote') === 'Yes',
        survey_comment: formData.get('survey-comment') || undefined,
        survey_email: formData.get('survey-email') || undefined,
    });
}
export default function survey() {
    // @ts-ignore
    if (window.IS_NEXTJS_PAGE)
        return;
    var form = document.querySelector('.js-survey');
    var texts = Array.from(document.querySelectorAll('.js-survey input, .js-survey textarea'));
    var votes = Array.from(document.querySelectorAll('.js-survey [type=radio]'));
    if (!form || !texts.length || !votes.length)
        return;
    form.addEventListener('submit', function (evt) {
        evt.preventDefault();
        submitForm(form);
        updateDisplay(form, 'end');
    });
    votes.forEach(function (voteEl) {
        voteEl.addEventListener('change', function (evt) {
            var radio = evt.target;
            var state = radio.value.toLowerCase();
            submitForm(form);
            updateDisplay(form, state);
        });
    });
    // Prevent the site search from overtaking your input
    texts.forEach(function (text) {
        text.addEventListener('keydown', function (evt) {
            if (evt.code === 'Slash')
                evt.stopPropagation();
        });
    });
}
